// –≠—Ç–∞ —Ñ—É–Ω–∫—Ü–∏—è –ø–æ –∏–¥–µ–µ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –∏–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–∞,
// –Ω–æ —É–ø—Ä–æ—â–µ–Ω–æ –∏ –Ω—É–∂–Ω–æ –µ—ë –ø—Ä–æ—Å—Ç–µ–π—à–∏–º –æ–±—Ä–∞–∑–æ–º —Ä–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å
// –º–æ–∂–µ—Ç –≤–µ—Ä–Ω—É—Ç—å –º–∞—Å—Å–∏–≤ –≤–∏–¥–∞ (null | {v: number})[] –∏–ª–∏ –≤–µ—Ä–Ω—É—Ç—å –æ—à–∏–±–∫—É –∫–ª–∞—Å—Å–∞ Error

const serverApiRequest = async a => {
  /*simulate request*/
  try {
    const response = await fetch(`//t.syshub.ru/${a}`); // –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —à–∞–±–ª–æ–Ω–Ω—ã—Ö —Å—Ç—Ä–æ–∫;
    const data = await response.json();
    if (!response.ok) {
      throw new Error(`${response.status}: ${data.error}`); //–æ–±—Ä–∞–±–æ—Ç–∫–∞ –æ—à–∏–±–æ–∫ —Å —Å–µ—Ä–≤–µ—Ä–∞
    }
    return data;
  } catch (e) {
    return `${e.name} ${e.message}`; //–≤—ã–≤–æ–¥ —Å–æ–æ–±—â–µ–Ω–∏—è –æ—à–∏–±–∫–∏
  }
};

// –ú–æ–∂–Ω–æ –≤—ã–ø–æ–ª–Ω–∏—Ç—å –ø–æ –∞–Ω–∞–ª–æ–≥–∏–∏ —Å serverApiRequest(), –∞ –º–æ–∂–Ω–æ –ª—É—á—à–µ, —Å–º. –ø–æ–¥—Å–∫–∞–∑–∫—É –Ω–∏–∂–µ
// –ù–µ –¥–æ–ª–∂–Ω–æ –ø—Ä–µ—Ä—ã–≤–∞—Ç—å –≤—ã–ø–æ–ª–Ω–µ–Ω–∏–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è –∏ –ª–æ–º–∞—Ç—å –µ–≥–æ, –µ—Å–ª–∏ —á—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫

const sendAnalytics = async (a, b) => {
  try {
    if (navigator.sendBeacon) {
      //–ø—Ä–æ–≤–µ—Ä—è–µ–º –ø–æ–¥–¥–µ—Ä–∂–∫—É –±—Ä–∞—É–∑–µ—Ä–æ–º
      const status = navigator.sendBeacon(a, b); //–∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ –º–µ—Ç–æ–¥–∞ sendBeacon
      return status ? "Successfully queued!" : "Failure."; //–∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —Ç–µ—Ä–Ω–∞—Ä–Ω–æ–≥–æ –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞ –¥–ª—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –æ—Ç–≤–µ—Ç–∞}
    }
    const { data } = await axios.post(a, b);
    return data;
  } catch (e) {
    return e;
  }
};
/* –ù—É–∂–Ω–æ:
    1 –°–¥–µ–ª–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é —Ä–∞–±–æ—á–µ–π –≤ –ø—Ä–∏–Ω—Ü–∏–ø–µ –Ω–µ –º–µ–Ω—è—è –ª–æ–≥–∏–∫–∏ –Ω–æ –¥–æ—Å—Ç—É–ø–Ω–æ ES8+
    2 –û–±—â–∞—è –ª–æ–≥–∏–∫–∞: –∑–∞–ø—Ä–æ—Å, –µ—Å–ª–∏ —É—Å–ø–µ—Ö, —Ç–æ –æ—Ç–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö –≤ –∞–Ω–∞–ª–∏—Ç–∏–∫—É, –æ–±—Ä–∞–±–æ—Ç–∫–∞ –¥–∞–Ω–Ω—ã—Ö –∏ –∏—Ö –≤–æ–∑–≤—Ä–∞—Ç
    3 –ü–æ–¥—Å–≤–µ—Ç–∏—Ç—å –º–µ—Å—Ç–∞, –≥–¥–µ –¢–ó –Ω–µ–¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ
    4 –ü–æ–¥—Å–≤–µ—Ç–∏—Ç—å –º–µ—Å—Ç–∞, –≤–µ—Ä–æ—è—Ç–Ω–æ –ø—Ä–æ–±–ª–µ–º–Ω—ã–µ
*/
const requestData = async ({ id, param }) => {
  // should return [null, {v: 1}, {v: 4}, null] or Error (may return array (null | {v: number})[])
  try {
    const response = await serverApiRequest(`query/data/${id}/param/${param}`); // –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —à–∞–±–ª–æ–Ω–Ω—ã—Ö —Å—Ç—Ä–æ–∫;
    if (/Error\b/.test(response)) {
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –æ—à–∏–±–æ–∫
      throw new Error(response);
    }
    sendAnalytics("/requestDone", {
      //–≤–µ—Ä–æ—è—Ç–Ω–æ, –ø—Ä–æ–±–ª–µ–º–Ω–æ–µ –º–µ—Å—Ç–æ, –æ—Ç–≤–µ—Ç –ø—Ä–∏—Ö–æ–¥–∏—Ç, –Ω–æ –º–µ—Ç–æ–¥ –±—ã–ª –æ—Ç–∫–ª—é—á–µ–Ω –∏ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω
      //–Ω–µ–¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –¢–ó: –Ω–µ—Ç API, –∫—É–¥–∞ –ø–æ—Å—ã–ª–∞–µ—Ç—Å—è –∑–∞–ø—Ä–æ—Å sendAnalytics
      type: "data",
      id, //–∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —Å–∏–Ω—Ç–∞–∫—Å–∏—Å–∞ ES6
      param //–∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —Å–∏–Ω—Ç–∞–∫—Å–∏—Å–∞ ES6
    });
    return response
      .filter(i => i !== null)
      .map(i => Object.values(i))
      .flat(); //–ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –¥–∞–Ω–Ω—ã–µ;
  } catch (e) {
    return e.message;
  }

  // after complete request if *not* Error call
};

