  // –î–ª—è –ª–∞–∫–æ–Ω–∏—á–Ω–æ—Å—Ç–∏ –∫–æ–¥–∞ –º–æ–∂–Ω–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å Promise.all
  log(await requestData({ id: 1, param: "any" }));
  log(await requestData({ id: 4, param: "string" }));
